import newsNavBar from "../../components/news/news-nav-bar.vue"
import commonList from "../../components/common/common-list.vue"
import loadMore from "../../components/common/load-more.vue"
import topicNav from "../../components/news/topic-nav.vue"
import topicList from "../../components/news/topic-list.vue"
import noThing from "../../components/common/no-thing.vue"
export default {
	components: {
		newsNavBar,
		commonList,
		loadMore,
		topicNav,
		topicList,
		noThing
	},
	data() {
		return {
			tabIndex: 0,
			swiperheight: 500,
			tabBars: [{
					name: "关注",
					id: "guanzhu"
				},
				{
					name: "话题",
					id: "huati"
				}
			],
			
			guanzhu: {
				firstload: false,
				loadtext: '上拉加载更多',
				list: [],
				page: 1,
			}
			,
			topic: {
				swiper: [],
				nav: [],
				list: []
			},	
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100);
				this.swiperheight = height;
			}
		}),
		this.__init();
		uni.$on('updateData',this.updateData);
	},
	onShow(){
		this.getFollowPostList();
	},
	methods: {
		//初始化数据
		__init(){
			this.getSwiper();
			this.getNav();
			this.getHot();
		},
		updateData(data){
			switch (data.type){
				case "support":
					this.updateSupport(data);
					break;
				case "updateComment":
					this.updateComment(data);
					break;	
			}
		},
		// 更新评论数
		updateComment(data){
			// 拿到当前对象
			let obj = this.guanzhu.list.find((val) => {
				return val.id === data.post_id;
			})
			if(!obj) return;
			obj.commentnum++;   // 评论数+1
		},
		//更新顶踩
		updateSupport(data){
			let obj = this.guanzhu.list.find((item)=>{
				return item.id === data.post_id;
			})
			if(!obj || obj.infonum.index === 1) return;
			if (data.do == 'ding') {
				obj.infonum.index = 1;
				obj.goodnum++;
			}
		},
		//获取关注列表数据
		async getFollowPostList(){
			let url = `/followpost/${this.guanzhu.page}`;
			let [err,res] = await this.$http.get(url,{},{
				token: true
			});
			//错误处理
			if(!this.$http.errorCheck(err,res)) {
				this.guanzhu.firstload = true;
				return this.guanzhu.loadtext = '上拉加载更多';
			}
			//成功
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push(this.__format(list[i]))
			}
			
			this.guanzhu.list = this.guanzhu.page > 1 ? this.guanzhu.list.concat(arr) : arr;
			this.guanzhu.firstload = true;
			this.guanzhu.loadtext = list.length < 10 ? '没有更多数据了' : '上拉加载更多';
			return;
		},
		//获取banner
		async getSwiper(){
			let [err,res] = await this.$http.get('/adsense/1');
			if(!this.$http.errorCheck(err,res)) return;
			let list = res.data.data.list;
			let arr = [];
			for (var i = 0; i < list.length; i++) {
				arr.push({
					src: list[i].src,
					url: list[i].url
				})
			}
			this.topic.swiper = arr;
		},
		//获取热门分类
		async getNav(){
			let[err,res] = await this.$http.get('/topicclass');
			if(!this.$http.errorCheck(err,res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					name:list[i].classname
				})
			}
			this.topic.nav = arr;
		},
		//获取热门话题
		async getHot(){
			let[err,res] = await this.$http.get('/hottopic');
			if(!this.$http.errorCheck(err,res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					titlepic:list[i].titlepic,
					title:list[i].title,
					desc:list[i].desc,
					totalnum:list[i].post_count,
					todaynum:list[i].todaypost_count,
				})
			}
			this.topic.list = arr;
		},
		//点击事件
		changeTab(index) {
			this.tabIndex = index
		},
		//滑动事件
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		loadmore() {
			if(this.guanzhu.loadtext!="上拉加载更多") return;
			this.guanzhu.loadtext = '加载中...';
			this.guanzhu.page++;
			this.getFollowPostList();
		},
		openSearch(){
			uni.navigateTo({
				url: '../search/search?searchType=topic'
			})
		},
		// 格式转化
		__format(item){
			return {
				userid:item.user.id,
				userpic:item.user.userpic,
				username:item.user.username,
				isguanzhu:!!item.user.fens.length,
				id:item.id,
				title:item.title,
				type:"img", // img:图文,video:视频
				titlepic:item.titlepic,
				video:false,
				path:item.path,
				share:!!item.share,
				infonum:{
					index:(item.support.length>0) ? (item.support[0].type+1) : 0,//0:没有操作，1:顶,2:踩；
					dingnum:item.ding_count,
					cainum:item.cai_count,
				},
				commentnum:item.comment_count,
				sharenum:item.sharenum,
			}
		},
	}
}
